@charset "UTF-8";
/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ul,
dl,
ol {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
  color: #000;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

img {
  border-style: none;
  height: auto;
  max-width: 100%;
  vertical-align: bottom;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  background-color: transparent;
}

textarea {
  display: block;
  overflow-y: auto;
  resize: vertical;
}

[type=submit],
[type=button],
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

[hidden] {
  display: none;
}

[disabled] {
  cursor: not-allowed;
}

:focus:not(.focus-visible) {
  outline: 0;
}

html,
body {
  height: 100vh;
}

html {
  font-size: 62.5%;
}

body {
  position: relative;
  overflow-x: hidden;
  font-family: "Noto Sans JP", "", YuGothic, " Medium", "Yu Gothic Medium", "", "Yu Gothic", "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "", "Meiryo", sans-serif;
  color: rgb(40, 40, 40);
  font-size: 16px; /*IE9*/
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.7;
}

body:not(.home)::before {
  display: block;
  padding: 72px 0 0;
  content: "";
}
@media screen and (max-width: 1024px) {
  body:not(.home)::before {
    padding: 72px 0 0;
  }
}
@media screen and (max-width: 767px) {
  body:not(.home)::before {
    padding: 64px 0 0;
  }
}

::placeholder {
  color: #ccc;
}

/*  */
strong,
b {
  font-weight: 700;
}

.u-hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  .u-hidden-pc {
    display: none !important;
  }
}

@media screen and (max-width: 1024px) {
  .u-hidden-tab {
    display: none !important;
  }
}

@media screen and (max-width: 767px) {
  .u-hidden-sp {
    display: none !important;
  }
}

/*----------------------------------------
	Media
----------------------------------------*/
@media screen and (max-width: 1024px) {
  .is-pc,
  .pc,
  .u-visible-pc {
    display: none !important;
  }
}
@media screen and (max-width: 767px) {
  .is-pc,
  .pc,
  .u-visible-pc {
    display: none !important;
  }
}

.is-tab,
.tablet,
.u-visible-tab {
  display: none !important;
}
@media screen and (max-width: 1024px) {
  .is-tab,
  .tablet,
  .u-visible-tab {
    display: block !important;
  }
}

.is-sp,
.sp,
.u-visible-sp {
  display: none !important;
}
@media screen and (max-width: 767px) {
  .is-sp,
  .sp,
  .u-visible-sp {
    display: block !important;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*------------------------------------------------------------------------
	etc
------------------------------------------------------------------------*/
@keyframes nav-link-enter {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes nav-link-leave {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(130%);
  }
}
@keyframes c-btn-enter {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes c-btn-leave {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes c-btn-arrow {
  0% {
    -webkit-transform: translateY(-50%) translateX(0);
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
  25% {
    -webkit-transform: translateY(-50%) translateX(3px);
    transform: translateY(-50%) translateX(3px);
    opacity: 0;
  }
  50% {
    -webkit-transform: translateY(-50%) translateX(6px);
    transform: translateY(-50%) translateX(6px);
    opacity: 0;
  }
  50.1% {
    -webkit-transform: translateY(-50%) translateX(-5px);
    transform: translateY(-50%) translateX(-5px);
    opacity: 0;
  }
  90% {
    -webkit-transform: translateY(-50%) translateX(0);
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(-50%) translateX(0);
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
}
body[data-status=loading] .l-loader-lower {
  pointer-events: auto;
  opacity: 1;
}

body[data-status=loadstart] .l-loader-lower {
  pointer-events: auto;
  opacity: 1;
}

.l-loader-lower {
  position: fixed;
  z-index: 99999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  transition: opacity cubic-bezier(0.36, 0.14, 0, 1) 1s;
}
.l-loader-lower__bg {
  position: absolute;
  z-index: 99999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
}

/*----------------------------------------
l-loader
----------------------------------------*/
.l-main {
  position: relative;
}

.l-col2 {
  display: flex;
  justify-content: space-between;
  margin: auto;
  max-width: 1200px;
  position: relative;
  width: 84.375%;
}
@media screen and (max-width: 767px) {
  .l-col2 {
    width: 100%;
    padding: 0 24px;
    display: block;
  }
}
.l-col2__main {
  width: 70.75%;
  max-width: 816px;
}
@media screen and (max-width: 767px) {
  .l-col2__main {
    max-width: 100%;
    width: 100%;
  }
}
.l-col2__aside {
  width: 29.25%;
  min-width: 200px;
  max-width: 320px;
  margin-left: 64px;
}
@media screen and (max-width: 1024px) {
  .l-col2__aside {
    margin-left: 40px;
  }
}
@media screen and (max-width: 767px) {
  .l-col2__aside {
    max-width: 100%;
    width: 100%;
    margin-left: 0;
    margin-top: 120px;
  }
}

.l-news {
  padding-bottom: 140px;
}
@media screen and (max-width: 767px) {
  .l-news {
    padding-bottom: 64px;
  }
}

.l-inner {
  max-width: 1200px;
  width: 84.375%;
  margin: 0 auto;
}
@media screen and (max-width: 767px) {
  .l-inner {
    width: 100%;
    padding: 0 24px;
  }
}
.l-inner--narrow {
  max-width: 1000px;
}
.l-inner--tiny {
  max-width: 800px;
}

.l-window-full {
  margin: 0 calc(50% - 50vw);
  overflow: hidden;
  width: 100vw;
}
.l-window-right {
  display: block;
  margin: 0 calc(50% - 50vw);
  overflow: hidden;
  padding: 0 0 0 calc(50vw - 50%);
  width: 100vw;
}
.l-window-left {
  display: block;
  margin: 0 calc(50% - 50vw);
  overflow: hidden;
  padding: 0 calc(50vw - 50%) 0 0;
  width: 100vw;
}

.l-footer {
  width: 100%;
  background: rgb(40, 40, 40);
  color: #fff;
}

.c-accordion {
  position: relative;
}
.c-accordion.is-active .c-accordion__btn::after {
  transform: translateY(-50%) rotate(180deg);
}
.c-accordion__btn::before, .c-accordion__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 32px;
  transform: translateY(-50%);
  width: 24px;
  height: 4px;
  background-color: #fff;
  border-radius: 50px;
  transition: 0.3s;
}
@media screen and (max-width: 767px) {
  .c-accordion__btn::before, .c-accordion__btn::after {
    width: 12px;
  }
}
.c-accordion__btn::after {
  transform: translateY(-50%) rotate(90deg);
}

.c-border-list__item {
  border-bottom: 1px solid #ddd;
}
.c-border-list__item:first-child {
  border-top: 1px solid #ddd;
}

.c-btn--left a,
.c-btn--left button {
  margin-right: auto;
}
.c-btn--right a,
.c-btn--right button {
  margin-left: auto;
}
.c-btn--center a,
.c-btn--center button {
  margin: 0 auto;
}

.c-border-btn a,
.c-border-btn button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 320px;
  width: 100%;
  padding: 16px 0;
  height: 60px;
  border: 1px solid #ccc;
  color: rgb(40, 40, 40);
  text-align: center;
  transition: 0.3s;
  border-radius: 50px;
}
@media screen and (max-width: 767px) {
  .c-border-btn a,
  .c-border-btn button {
    max-width: 100%;
    height: 56px;
  }
}
.c-border-btn--white a,
.c-border-btn--white button {
  border: 2px solid #fff;
  color: #fff;
}

.c-ar-btn {
  display: flex;
  justify-content: center;
}
.c-ar-btn a {
  display: inline-block;
  border-radius: 50px;
  background: linear-gradient(45deg, #51d1e2, #dedc12);
  padding: 2px;
  transition: 0.3s;
}
.c-ar-btn a:hover {
  box-shadow: 0 8px 16px rgba(0, 12, 16, 0.16);
}
.c-ar-btn a span {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 256px;
  width: 100%;
  height: 48px;
  padding: 8px 24px;
  background-color: #fff;
  border-radius: 50px;
  color: rgb(40, 40, 40);
  font-weight: 700;
}
.c-ar-btn a span svg {
  margin-left: 8px;
}

.c-shadow-btn a,
.c-shadow-btn button {
  background: transparent;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 264px;
  width: 100%;
  height: 56px;
  padding: 8px 16px;
  color: rgb(40, 40, 40);
  transition: 0.3s;
  font-weight: 700;
  z-index: 0;
  background-color: #fff;
  border-radius: 50px;
  box-shadow: 0 4px 10px -2px rgba(0, 16, 14, 0.08), 0 4px 20px 8px rgba(0, 16, 14, 0.03);
}
@media screen and (max-width: 767px) {
  .c-shadow-btn a,
  .c-shadow-btn button {
    max-width: 100%;
    height: 56px;
  }
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn a:hover,
  .c-shadow-btn button:hover {
    background-color: #f0f1f1;
    box-shadow: none;
    transform: translateY(2px);
  }
}
.c-shadow-btn--blue a,
.c-shadow-btn--blue button {
  color: #fff;
  background-color: #51d1e2;
  box-shadow: 0px 4px 10px -2px rgba(81, 209, 226, 0.5), 0px 16px 32px 0px rgba(81, 209, 226, 0.5);
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--blue a:hover,
  .c-shadow-btn--blue button:hover {
    background-color: #44bece;
    box-shadow: none;
  }
}
.c-shadow-btn--yellow a,
.c-shadow-btn--yellow button {
  color: #fff;
  background-color: #dedc12;
  box-shadow: 0 4px 10px -2px rgba(211, 208, 16, 0.4), 0 16px 32px 0px rgba(211, 208, 16, 0.2);
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--yellow a:hover,
  .c-shadow-btn--yellow button:hover {
    background-color: #cac70f;
    box-shadow: none;
  }
}
.c-shadow-btn--kv a {
  border: 2px solid #51d1e2;
  background-color: #fff;
  color: #51d1e2;
  max-width: 264px;
  width: 100%;
  height: 56px;
}
@media screen and (max-width: 767px) {
  .c-shadow-btn--kv a {
    height: 48px;
  }
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--kv a:hover {
    box-shadow: 0 8px 16px rgba(81, 209, 226, 0.5);
  }
}
.c-shadow-btn--gradient a {
  position: relative;
  background-image: linear-gradient(to right, #51d1e2 0%, #dedc12 100%);
  color: #fff;
  box-shadow: none;
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--gradient a:hover::before {
    opacity: 0.5;
  }
}
.c-shadow-btn--gradient a::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 16px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-radius: 50px;
  background: linear-gradient(to right, #51d1e2 0%, #dedc12 100%);
  filter: blur(32px);
  opacity: 0;
  transition: 0.3s;
}
.c-shadow-btn--gradient a svg {
  margin-left: 8px;
}
.c-shadow-btn--w-blue a {
  background-color: #fff;
  color: #51d1e2;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .c-shadow-btn--w-blue a {
    height: 56px;
  }
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--w-blue a:hover {
    box-shadow: 0 8px 16px rgba(81, 209, 226, 0.5);
  }
}
.c-shadow-btn--w-yellow a {
  background-color: #fff;
  color: #dedc12;
  width: 100%;
  box-shadow: 0 8px 16px rgba(211, 208, 16, 0.5);
}
@media screen and (max-width: 767px) {
  .c-shadow-btn--w-yellow a {
    height: 48px;
  }
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--w-yellow a:hover {
    background-color: #f0f1f1;
    box-shadow: none;
  }
}
.c-shadow-btn--a-blue a, .c-shadow-btn--a-yellow a {
  max-width: 340px;
  width: 100%;
  height: 80px;
  box-shadow: 0 8px 16px rgba(26, 25, 2, 0.16);
  font-size: 20px; /*IE9*/
  font-size: 2rem;
}
@media screen and (max-width: 767px) {
  .c-shadow-btn--a-blue a, .c-shadow-btn--a-yellow a {
    height: 56px;
    font-size: 16px; /*IE9*/
    font-size: 1.6rem;
  }
}
.c-shadow-btn--a-blue a::after, .c-shadow-btn--a-yellow a::after {
  width: 24px;
  height: 12px;
}
@media screen and (min-width: 1025px) {
  .c-shadow-btn--a-blue a:hover, .c-shadow-btn--a-yellow a:hover {
    background-color: #f0f1f1;
    box-shadow: none;
  }
}
.c-shadow-btn--a-blue a {
  background-color: #fff;
  color: #51d1e2;
}
.c-shadow-btn--a-blue a::after {
  background-image: url(../img/icon/arrow_blue_short.svg);
}
.c-shadow-btn--a-yellow a {
  background-color: #fff;
  color: #dedc12;
}
.c-shadow-btn--a-yellow a::after {
  background-image: url(../img/icon/arrow_yellow_short.svg);
}

.c-btn-modal-close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: calc(100% + 60px);
  margin-bottom: -30px;
  margin-left: -30px;
  height: 60px;
  background-color: rgb(40, 40, 40);
  color: #fff;
  font-size: 15px; /*IE9*/
  font-size: 1.5rem;
  line-height: 1;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .c-btn-modal-close {
    border-radius: 0 0 12px 12px;
  }
}

.c-text-btn {
  position: relative;
  padding-left: 28px;
  color: rgb(40, 40, 40);
  font-size: 14px; /*IE9*/
  font-size: 1.4rem;
  letter-spacing: 0.06em;
  line-height: 1.7;
  transition: opacity 0.3s;
}
.c-text-btn::before {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 24px;
  height: 12px;
  transform: translateY(-50%);
  background-image: url(../img/icon/arrow_black_short.svg);
  background-position: left center;
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
}
@media screen and (max-width: 767px) {
  .c-text-btn::before {
    width: 41px;
    height: 11px;
  }
}

.swiper {
  position: relative;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  color: rgb(40, 40, 40);
  font-size: 32px; /*IE9*/
  font-size: 3.2rem;
}

.swiper-button-prev {
  left: -18px;
}

.swiper-button-next {
  right: -18px;
}

.swiper-button-disabled {
  background-color: #f8f8f8;
  box-shadow: none;
  pointer-events: none;
}
.swiper-button-disabled::before {
  filter: grayscale(100%);
}

.swiper-pagination {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  margin-top: 20px;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .swiper-pagination {
    gap: 4px;
  }
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.3s ease;
  vertical-align: top;
  background-color: #828282;
}

.swiper-pagination-bullet-active {
  background: rgb(40, 40, 40) !important;
}

.swiper-scrollbar {
  position: relative;
  margin-top: 5px;
  border-radius: 12px;
}
.swiper-scrollbar::after {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
  margin: auto;
  content: "";
  background-color: #ddd;
  border-radius: 50px;
}

.swiper-scrollbar-drag {
  height: 5px;
  cursor: grab;
  border-radius: 50px;
  background-color: #828282;
  background-clip: content-box;
}

.swiper-slide-thumb-active::before {
  background: transparent !important;
}

.swiper-fade .swiper-slide {
  transition-property: opacity !important;
}

.c-figure {
  position: relative;
  width: 100%;
  padding: 56.25% 0 0;
  overflow: hidden;
}
.c-figure__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.c-figure--vertical {
  padding: 156% 0 0;
}
.c-figure--square {
  padding: 100% 0 0;
}
.c-figure--square {
  padding: 100% 0 0;
}

.c-faq-list {
  width: 100%;
}
.c-faq-list__item {
  border-radius: 12px;
  border: 1px solid transparent;
  background-color: #fff;
  padding: 0 40px;
  transition: 0.3s;
}
@media screen and (max-width: 767px) {
  .c-faq-list__item {
    padding: 0 16px;
  }
}
.c-faq-list__item:not(:last-child) {
  margin-bottom: 20px;
}
@media screen and (max-width: 767px) {
  .c-faq-list__item:not(:last-child) {
    margin-bottom: 8px;
  }
}
.c-faq-list__item.is-active .c-faq-list__icon::after {
  opacity: 0;
}
.c-faq-list__item.is-active .c-faq-list__answer {
  opacity: 1;
}
