/*----------------------------------------
l-loader
----------------------------------------*/
body[data-status=loading] .l-loader {
  pointer-events: auto;
  opacity: 1;
}
body[data-status=loading] .l-loader-bg {
  transform: translateX(0);
}
body[data-status=loading] .l-loader-inner {
  opacity: 1;
}
body[data-status=loading] .p-gnav-link::before,
body[data-status=loading] .p-header-contact-block::before {
  animation-duration: 0s;
}

body[data-status=loadstart] .l-loader {
  pointer-events: auto;
  opacity: 1;
}
body[data-status=loadstart] .l-loader-bg {
  animation-name: loadstart;
}
body[data-status=loadstart] .l-loader-inner {
  opacity: 1;
  transition-duration: 0.6s;
}
body[data-status=loadstart] .l-loader-progress {
  left: 0 !important;
  transition-duration: 0s;
}

.l-loader {
  position: fixed;
  z-index: 99999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
}
.l-loader-bg {
  position: absolute;
  z-index: 99999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  transition: transform cubic-bezier(0.19, 1, 0.22, 1) 1.5s;
  transform: translateX(100%);
  animation: cubic-bezier(0.19, 1, 0.22, 1) 1s forwards;
}
.l-loader-inner {
  position: absolute;
  z-index: 999999;
  top: 50%;
  left: 50%;
  opacity: 0;
  transition: opacity cubic-bezier(0.19, 1, 0.22, 1) 1.5s;
  transform: translate(-50%, -50%);
}
.l-loader-progress {
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.8);
  transition: left cubic-bezier(0.19, 1, 0.22, 1) 0.5s;
}
.l-loader-logo {
  max-width: 280px;
}
.l-loader-logo-img {
  width: 100%;
  height: auto;
}
.l-loader-text {
  font-family: "Poppins", sans-serif;
  font-size: 28px; /*IE9*/
  font-size: 2.8rem;
}

@-webkit-keyframes loadstart {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes loadstart {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
